
.page-navigation {
  display: flex;

  margin: 0;
  padding: 0;
  list-style: none;
  overflow: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    width: 0;
    background-color: transparent;
  }
}

@media (min-width: $desktop-width) {
  .page-navigation {
    flex-wrap: wrap;
    overflow: hidden;
  }
}


.page-navigation__link {
  border: 2px solid transparent;
  display: flex;
  padding: 8px 21px;
  color:#7e7e82;
  text-decoration: none;
  white-space: nowrap;

  &--active {
    border-color:#b8ffec;
    background-color: #ffffff;
  }
}
